$traceurRuntime.ModuleStore.getAnonymousModule(function() {
  "use strict";
  angular.module('app.controllers').controller('ChooseCtrl', function ChooseCtrl($scope, $state) {
    console.log('choose');
    $scope.navCaption = 'choose_header';
  }).controller('PageCtrl', function PageCtrl($scope, $rootScope, $state, $stateParams, $sce) {
    console.log($stateParams, $state);
    $scope.stateName = $state.$current.name;
    $scope.navCaption = $scope.stateName + '_header';
    $scope.url = "http://goldenage.miraclethings.nl/app/" + $scope.stateName;
    $scope.url = $sce.trustAsResourceUrl($scope.url);
  });
  ;
  return {};
});

