$traceurRuntime.ModuleStore.getAnonymousModule(function() {
  "use strict";
  angular.module('app.controllers').controller('HashtagCtrl', function HashtagCtrl($scope, $window, $stateParams, story, StoryContainer) {
    $scope.hashtag = story.hashtags[$stateParams.hashtagId];
    if (!$scope.hashtag) {
      $window.history.back();
      return;
    }
    var container = new StoryContainer(story);
    $scope.cards = container.filterHashtagCards($stateParams.hashtagId);
  });
  ;
  return {};
});

