$traceurRuntime.ModuleStore.getAnonymousModule(function() {
  "use strict";
  angular.module('app.controllers').controller('GroupCtrl', function ProfileCtrl($scope, $stateParams, $window, story) {
    $scope.group = story.groups[$stateParams.groupId];
    if (!$scope.group) {
      $window.history.back();
      return;
    }
  });
  ;
  return {};
});

