$traceurRuntime.ModuleStore.getAnonymousModule(function() {
  "use strict";
  angular.module('app.controllers').controller('AppCtrl', function AppCtrl($scope, $modal, $rootScope, $state, APIService, AuthState, AuthService) {
    $scope.$state = $state;
    $scope.state = AuthState;
    $rootScope.lang = navigator.language.substr(0, 2) == 'nl' ? 'nl' : 'en';
    $scope.blurred = true;
    APIService.get('goldenage/stories').then(function(r) {
      AuthState.stories = r;
      return true;
    });
    $scope.logout = function() {
      AuthService.logout();
    };
  }).controller('StartCtrl', function StartCtrl($scope, $state, AuthService, AuthState) {
    AuthService.isAuthorized().then(function(auth) {
      $state.go('choose');
    }, function() {
      $state.go('login');
    });
  });
  ;
  return {};
});

