$traceurRuntime.ModuleStore.getAnonymousModule(function() {
  "use strict";
  angular.module('app.directives').filter('unsafe', function($sce) {
    return $sce.trustAsHtml;
  }).directive('scrollContainer', function($window, $timeout) {
    return {
      restrict: 'A',
      scope: {scrollContainer: '@'},
      link: function(s, el) {
        el.addClass("scroll-area");
        el.css('opacity', 0);
        el.css('height', ($(window).height() - $(el).offsetParent().offset().top) + 'px');
        if (s.scrollContainer == 'force') {
          console.log('a');
          el.css('opacity', 1);
          return;
        }
        function resize() {
          var h = $(el).height();
          var newh = $(window).height() - $(el).offsetParent().offset().top;
          if (newh < h) {
            $(el).css("height", newh + "px");
          } else {}
          el.toggleClass("scrollbar", (el[0].scrollHeight > el[0].clientHeight));
          el.css('opacity', 1);
        }
        $timeout(resize, 0);
        $timeout(resize, 800);
        $(window).on('resize', _.debounce(resize, 500));
      }
    };
  }).directive('subnavHeader', function($window) {
    return {
      restrict: 'E',
      templateUrl: 'app/templates/ui/subnav_header.html',
      scope: {title: '@'},
      link: function(scope) {
        scope.back = function() {
          $window.history.back();
        };
      }
    };
  }).directive('cardImage', function($sce, $compile, $state, i18n, Constants, actorClick) {
    return {
      restrict: 'E',
      scope: {
        card: '=',
        story: '=',
        profile: '=',
        who: '@'
      },
      template: '<img ng-src="{{ image }}" ng-show="visible" class="card-profile-image" ng-click="click()" />',
      replace: true,
      link: function(scope, ele) {
        scope.visible = !!scope.card[scope.who];
        if (!scope.visible)
          return;
        var id = scope.card[scope.who];
        var rsc = scope.story.persons[id] || scope.story.groups[id];
        if (id == Constants.YOU) {
          rsc = scope.profile;
        }
        if (rsc) {
          scope.image = rsc.image;
        }
        scope.click = function() {
          actorClick(id, scope.story);
        };
      }
    };
  }).factory('actorClick', function($state, Constants) {
    return function(id, story) {
      if (id == Constants.YOU) {
        $state.go('story.profile', {storyId: story.id});
        return;
      }
      if (story.persons[id]) {
        $state.go('story.person', {
          storyId: story.id,
          personId: id
        });
        return;
      }
      if (story.groups[id]) {
        $state.go('story.group', {
          storyId: story.id,
          groupId: id
        });
        return;
      }
    };
  }).directive('cardTitle', function($sce, $compile, $state, i18n, Constants, actorClick) {
    return {
      restrict: 'E',
      scope: {
        card: '=',
        story: '=',
        profile: '='
      },
      replace: true,
      link: function(scope, ele) {
        var cat = scope.card.category;
        switch (cat) {
          case 'checkin':
            if (scope.card.target > 0) {
              cat += '_target';
            }
            break;
        }
        var template = i18n('card_title_' + cat);
        function link(tpl, id) {
          var rsc = scope.story.persons[id] || scope.story.groups[id];
          if (id == Constants.YOU) {
            rsc = scope.profile;
          }
          var a = "<a href ng-click='click(" + id + ")'>" + rsc.title + "</a>";
          return template.replace(tpl, a);
        }
        if (scope.card.author) {
          template = link('[author]', scope.card.author);
        }
        if (scope.card.target) {
          template = link('[target]', scope.card.target);
        }
        scope.click = function(id) {
          return actorClick(id, scope.story);
        };
        ele.html("<div class='card-title'>" + template + "</div>");
        $compile(ele.contents())(scope);
      }
    };
  }).directive('cardStatusImage', function() {
    return {
      restrict: 'E',
      scope: {card: '='},
      template: '<img ng-src="{{ image }}" ng-show="image" class="card-status" />',
      replace: true,
      link: function(scope, ele) {
        scope.image = '/img/ic_card_type_checkin.png';
        switch (scope.card.category) {
          case "checkin":
            scope.image = '/img/ic_card_type_checkin.png';
            break;
          case "friend_request":
            scope.image = '/img/ic_card_type_friend.png';
            break;
          case "friendship":
            scope.image = '/img/ic_card_type_friend.png';
            break;
          case "private_message":
            scope.image = '/img/ic_card_type_message.png';
            break;
          case "status_update":
            scope.image = '/img/ic_card_type_message.png';
            break;
          case "attend_event":
            scope.image = '/img/ic_card_type_event.png';
            break;
          case "tag_picture":
            scope.image = '/img/ic_card_type_tag.png';
            break;
          case "join_group":
            scope.image = '/img/ic_card_type_friend.png';
            break;
        }
      }
    };
  });
  ;
  return {};
});

